.status-projects {
    .table-data {
        margin-top: 20px;
        table {
            border-collapse: collapse;
        }

        tr {
            border-bottom: 1px solid #e7e7e8;
        }

        table tbody tr td {
            text-align: left;
        }
    }
    .qualify-experience {
        margin-top: 50px;
        &-title {
            gap: 10px;
            margin-bottom: 20px;
        }

        .stars,
        .stars label::before {
            display: inline-block;
        }

        .stars label:hover,
        .stars label:hover ~ label {
            color: #6f3bff;
        }

        .stars input {
            display: none;
        }

        .stars {
            direction: rtl;
        }

        .stars label {
            color: #ccd0fb;
        }

        .stars label::before {
            content: "\2605";
            width: 18px;
            line-height: 18px;
            text-align: center;
            font-size: 20px;
            padding: 8px;
            cursor: pointer;
        }

        .stars input:checked ~ label {
            color: #6f3bff;
        }

        .comments {
            width: fit-content;
            &-textarea {
                border-radius: 10px;
                width: 613px;
                height: 110px;
                border: 1px solid #70707042;
                margin-top: 10px;
                margin-bottom: 20px;
                resize: none;
                padding: 10px;
                box-sizing: border-box;
            }
        }

        .section-buttons {
            justify-content: end;
        }
    }
}
