.login {
    background-color: #6f3bff;
    width: 100%;
    height: 100vh;
    background-image: url(../../icons/background_transparent.png);
    background-repeat: no-repeat;
    background-position-y: center;
    background-position-x: -7em;

    .flex {
        height: 100%;
    }

    .logo-main,
    .inputs-main {
        flex: 1;
        text-align: center;
    }

    .inputs {
        max-width: 413px;
        height: 310px;
        background-color: rgba(255, 255, 255, 0.8);
        border-radius: 20px;
        margin: auto;
        padding: 20px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .recovery {
            text-align: left;
        }

        .button-blue {
            margin: 0 auto;
        }

        .not-account {
            display: flex;
            justify-content: space-between;
        }
    }
}
