.modal {
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.4);

    .modal-content {
        background-color: #fefefe;
        margin: 15% auto;
        padding: 20px 28px;
        border: 1px solid #888;
        width: 476px;
        height: 341px;
        border-radius: 25px;
    }
}

.modal-zoom-img {
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.7);

    .modal-content {
        margin: 15% auto;
        width: fit-content;
        padding: 0;
    }
}

.modal-message {
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.2);
    .modal-content {
        height: auto;
        border: none;
    }
}
